import { NextRequest, NextResponse } from 'next/server';
import { auth } from '@clerk/nextjs/server';
import { generateCode } from '@/services/referralService';
import { logError } from '@/utils/logger';

/**
 * POST /api/referrals/generate
 * Generate a unique referral code for the authenticated user
 *
 * @returns { code: string, shareUrl: string, expiresAt: string }
 */
export async function POST(request: NextRequest) {
  try {
    // Authenticate user
    const { userId } = await auth();
    if (!userId) {
      return NextResponse.json(
        { error: 'Unauthorized' },
        { status: 401 }
      );
    }

    // Generate referral code
    const code = await generateCode(userId);

    // Generate share URL
    const appUrl = process.env.NEXT_PUBLIC_APP_URL || 'https://relay.app';
    const shareUrl = `${appUrl}/signup?ref=${code}`;

    // Calculate expiry (1 year from now)
    const expiresAt = new Date(Date.now() + 365 * 24 * 60 * 60 * 1000).toISOString();

    return NextResponse.json({
      code,
      shareUrl,
      expiresAt,
    });
  } catch (error) {
    logError('Failed to generate referral code', error);
    return NextResponse.json(
      { error: 'Failed to generate referral code' },
      { status: 500 }
    );
  }
}
