import { db, Timestamp, COLLECTIONS, FieldValue } from '@/lib/firebase';
import { User, Referral } from '@/types/models';
import { NotFoundError } from '@/utils/error';
import { logDbOperation, logInfo, logError } from '@/utils/logger';
import { nanoid } from 'nanoid';

/**
 * Referral Service - Handles referral code generation, redemption, and credit management
 *
 * Business Rules:
 * - Each user gets a unique referral code
 * - Referrer earns £5 credit when new user signs up and creates first invoice
 * - New user earns £5 credit upon signup
 * - Credits can be used toward subscription or cashed out at £20 threshold
 * - £20 minimum payout threshold
 */

const REFERRAL_CODE_LENGTH = 12;
const REFERRER_CREDIT_AMOUNT = 5; // £5
const NEW_USER_CREDIT_AMOUNT = 5; // £5
const PAYOUT_THRESHOLD = 20; // £20
const REFERRAL_CODE_EXPIRY_DAYS = 365; // 1 year

/**
 * Generate unique referral code for a user
 * Format: 12 uppercase alphanumeric characters (e.g., ABC123XYZ789)
 */
export async function generateCode(freelancerId: string): Promise<string> {
  const startTime = Date.now();

  try {
    // Check if user already has a referral code
    const userDoc = await db.collection(COLLECTIONS.USERS).doc(freelancerId).get();
    if (!userDoc.exists) {
      throw new NotFoundError('User not found');
    }

    const user = userDoc.data() as User;
    if (user.referralCode) {
      logInfo('User already has referral code', { userId: freelancerId, code: user.referralCode });
      return user.referralCode;
    }

    // Generate unique code
    let code: string;
    let isUnique = false;
    let attempts = 0;
    const maxAttempts = 5;

    while (!isUnique && attempts < maxAttempts) {
      // Generate random alphanumeric code
      code = nanoid(REFERRAL_CODE_LENGTH).toUpperCase();

      // Check uniqueness
      const existingUser = await db
        .collection(COLLECTIONS.USERS)
        .where('referralCode', '==', code)
        .limit(1)
        .get();

      if (existingUser.empty) {
        isUnique = true;
      }
      attempts++;
    }

    if (!isUnique) {
      throw new Error('Failed to generate unique referral code after multiple attempts');
    }

    // Save code to user document
    await userDoc.ref.update({
      referralCode: code!,
      referralCreditsEarned: 0,
      referralCreditsSpent: 0,
      referralCreditsBalance: 0,
      updatedAt: Timestamp.now(),
    });

    logDbOperation('generate_referral_code', COLLECTIONS.USERS, freelancerId, Date.now() - startTime);
    logInfo('Generated referral code', { userId: freelancerId, code: code! });

    return code!;
  } catch (error) {
    logError('Failed to generate referral code', error);
    throw error;
  }
}

/**
 * Redeem a referral code for a new user
 * Awards credits to both referrer and new user
 */
export async function redeemCode(
  code: string,
  newUserId: string
): Promise<{ success: boolean; creditAdded: number; message: string }> {
  const startTime = Date.now();

  try {
    // Find referrer by code
    const referrerQuery = await db
      .collection(COLLECTIONS.USERS)
      .where('referralCode', '==', code.toUpperCase())
      .limit(1)
      .get();

    if (referrerQuery.empty) {
      return {
        success: false,
        creditAdded: 0,
        message: 'Invalid referral code',
      };
    }

    const referrerDoc = referrerQuery.docs[0];
    const referrer = referrerDoc.data() as User;

    // Check if user is trying to refer themselves
    if (referrer.userId === newUserId) {
      return {
        success: false,
        creditAdded: 0,
        message: 'Cannot use your own referral code',
      };
    }

    // Check if new user already used a referral code
    const newUserDoc = await db.collection(COLLECTIONS.USERS).doc(newUserId).get();
    if (!newUserDoc.exists) {
      throw new NotFoundError('New user not found');
    }

    const newUser = newUserDoc.data() as User;
    if (newUser.referredBy) {
      return {
        success: false,
        creditAdded: 0,
        message: 'You have already used a referral code',
      };
    }

    // Create referral record
    const referralId = nanoid();
    const now = Timestamp.now();

    await db.collection(COLLECTIONS.REFERRALS).doc(referralId).set({
      referralId,
      referrerId: referrer.userId,
      referredUserId: newUserId,
      referralCode: code.toUpperCase(),
      status: 'active',
      referrerCredit: REFERRER_CREDIT_AMOUNT,
      referredCredit: NEW_USER_CREDIT_AMOUNT,
      creditType: 'account_credit',
      signupDate: now,
      createdAt: now,
      expiresAt: Timestamp.fromDate(new Date(Date.now() + REFERRAL_CODE_EXPIRY_DAYS * 24 * 60 * 60 * 1000)),
    });

    // Award credits to both users
    await db.collection(COLLECTIONS.USERS).doc(referrer.userId).update({
      referralCreditsEarned: FieldValue.increment(REFERRER_CREDIT_AMOUNT),
      referralCreditsBalance: FieldValue.increment(REFERRER_CREDIT_AMOUNT),
      updatedAt: now,
    });

    await db.collection(COLLECTIONS.USERS).doc(newUserId).update({
      referredBy: referrer.userId,
      referralCreditsEarned: FieldValue.increment(NEW_USER_CREDIT_AMOUNT),
      referralCreditsBalance: FieldValue.increment(NEW_USER_CREDIT_AMOUNT),
      updatedAt: now,
    });

    logDbOperation('redeem_referral_code', COLLECTIONS.REFERRALS, referralId, Date.now() - startTime);
    logInfo('Referral code redeemed', {
      referralId,
      referrerId: referrer.userId,
      newUserId,
      code: code.toUpperCase(),
    });

    return {
      success: true,
      creditAdded: NEW_USER_CREDIT_AMOUNT,
      message: `£${NEW_USER_CREDIT_AMOUNT} credit added to your account!`,
    };
  } catch (error) {
    logError('Failed to redeem referral code', error);
    throw error;
  }
}

/**
 * Get referral statistics for a user
 */
export async function getReferralStats(userId: string): Promise<{
  referralsMade: number;
  creditsEarned: number;
  creditsBalance: number;
  referralCode: string;
}> {
  const startTime = Date.now();

  try {
    // Get user data
    const userDoc = await db.collection(COLLECTIONS.USERS).doc(userId).get();
    if (!userDoc.exists) {
      throw new NotFoundError('User not found');
    }

    const user = userDoc.data() as User;

    // Count active referrals
    const referralsQuery = await db
      .collection(COLLECTIONS.REFERRALS)
      .where('referrerId', '==', userId)
      .where('status', '==', 'active')
      .get();

    logDbOperation('get_referral_stats', COLLECTIONS.USERS, userId, Date.now() - startTime);

    return {
      referralsMade: referralsQuery.size,
      creditsEarned: user.referralCreditsEarned || 0,
      creditsBalance: user.referralCreditsBalance || 0,
      referralCode: user.referralCode || '',
    };
  } catch (error) {
    logError('Failed to get referral stats', error);
    throw error;
  }
}

/**
 * Get top referrers leaderboard
 */
export async function topReferrers(
  limit: number = 10
): Promise<
  Array<{
    userId: string;
    name: string;
    creditsEarned: number;
    referralCount: number;
  }>
> {
  const startTime = Date.now();

  try {
    // Get all users sorted by referral credits
    const usersQuery = await db
      .collection(COLLECTIONS.USERS)
      .where('referralCreditsEarned', '>', 0)
      .orderBy('referralCreditsEarned', 'desc')
      .limit(limit)
      .get();

    const leaderboard = await Promise.all(
      usersQuery.docs.map(async (doc) => {
        const user = doc.data() as User;

        // Count referrals for this user
        const referralsQuery = await db
          .collection(COLLECTIONS.REFERRALS)
          .where('referrerId', '==', user.userId)
          .where('status', '==', 'active')
          .get();

        return {
          userId: user.userId,
          name: user.name || 'Anonymous',
          creditsEarned: user.referralCreditsEarned || 0,
          referralCount: referralsQuery.size,
        };
      })
    );

    logDbOperation('get_top_referrers', COLLECTIONS.USERS, undefined, Date.now() - startTime);

    return leaderboard;
  } catch (error) {
    logError('Failed to get top referrers', error);
    throw error;
  }
}

/**
 * Get referral balance for a user
 */
export async function getReferralBalance(userId: string): Promise<number> {
  const startTime = Date.now();

  try {
    const userDoc = await db.collection(COLLECTIONS.USERS).doc(userId).get();
    if (!userDoc.exists) {
      throw new NotFoundError('User not found');
    }

    const user = userDoc.data() as User;
    logDbOperation('get_referral_balance', COLLECTIONS.USERS, userId, Date.now() - startTime);

    return user.referralCreditsBalance || 0;
  } catch (error) {
    logError('Failed to get referral balance', error);
    throw error;
  }
}

/**
 * Request payout of referral credits (min £20)
 * Creates a payout request that admin must approve
 */
export async function requestPayout(
  userId: string,
  amount: number
): Promise<{ success: boolean; payoutId: string; status: string; message?: string }> {
  const startTime = Date.now();

  try {
    // Validate amount
    if (amount < PAYOUT_THRESHOLD) {
      return {
        success: false,
        payoutId: '',
        status: 'rejected',
        message: `Minimum payout amount is £${PAYOUT_THRESHOLD}`,
      };
    }

    // Check user balance
    const userDoc = await db.collection(COLLECTIONS.USERS).doc(userId).get();
    if (!userDoc.exists) {
      throw new NotFoundError('User not found');
    }

    const user = userDoc.data() as User;
    const balance = user.referralCreditsBalance || 0;

    if (balance < amount) {
      return {
        success: false,
        payoutId: '',
        status: 'rejected',
        message: `Insufficient balance. You have £${balance} available`,
      };
    }

    // Create payout request (to be processed by admin/Stripe)
    const payoutId = nanoid();
    const now = Timestamp.now();

    // Note: referral_payouts collection will be created on first write
    await db.collection('referral_payouts').doc(payoutId).set({
      payoutId,
      userId,
      amount,
      status: 'pending',
      requestedAt: now,
      createdAt: now,
    });

    logDbOperation('request_payout', 'referral_payouts', payoutId, Date.now() - startTime);
    logInfo('Payout requested', { userId, amount, payoutId });

    return {
      success: true,
      payoutId,
      status: 'pending',
      message: 'Payout request submitted. Will be processed within 3-5 business days',
    };
  } catch (error) {
    logError('Failed to request payout', error);
    throw error;
  }
}

/**
 * Process payout (admin function)
 * Deducts credits from user and marks payout as completed
 */
export async function processPayout(
  userId: string
): Promise<{ transactionId: string; status: string }> {
  const startTime = Date.now();

  try {
    // Get pending payout requests for user
    const payoutsQuery = await db
      .collection('referral_payouts')
      .where('userId', '==', userId)
      .where('status', '==', 'pending')
      .limit(1)
      .get();

    if (payoutsQuery.empty) {
      throw new Error('No pending payout found for user');
    }

    const payoutDoc = payoutsQuery.docs[0];
    const payout = payoutDoc.data();

    // Deduct credits from user
    await db.collection(COLLECTIONS.USERS).doc(userId).update({
      referralCreditsBalance: FieldValue.increment(-payout.amount),
      referralCreditsSpent: FieldValue.increment(payout.amount),
      updatedAt: Timestamp.now(),
    });

    // Mark payout as paid
    await payoutDoc.ref.update({
      status: 'paid',
      paidAt: Timestamp.now(),
      stripePayoutId: `payout_${nanoid()}`, // Placeholder - integrate with Stripe
    });

    logDbOperation('process_payout', 'referral_payouts', payoutDoc.id, Date.now() - startTime);
    logInfo('Payout processed', { userId, amount: payout.amount, payoutId: payoutDoc.id });

    return {
      transactionId: payoutDoc.id,
      status: 'paid',
    };
  } catch (error) {
    logError('Failed to process payout', error);
    throw error;
  }
}
